<template>
  <!--接收详情页面-->
  <div class="container-details">
    <div class="scroll-wrap" :class="{'scroll-wrap-bottom': showBtn}">
      <!--头部名片信息区域-->
      <div class="card-area">
        <v-feed
          v-if="receiveData"
          :imgUrl = "getIconUrl(receiveData.noType)"
          :titleDetails = "receiveData.noTitle"
          :subtitle = "getPublisher(publisher)"
          :time = "getTime(receiveData.noReceipt, receiveData.noReceiptTime)"
          :receiptInfo="getReceiptInfo(receiveData.noReceipt,receiveData.noReceiptTime,receiveData.nrReceipt)"
          :showRelation="true"
          :myFeedId="myFeedId"
          :friendFeedId="friendFeedId"
        > </v-feed>
      </div>
      <!--中间文本信息区域-->
      <div class="content-text">
        <pre class="p-details">{{contentText}}</pre>
        <!--详情图片-->
        <div class="details-image" v-if="pictureUrl">
          <!--<img :src="item.npUrl" alt="" v-for="item in pictureUrl">-->
          <div class="img-wrap" v-for="item in pictureUrl">
            <img :src="item.npUrl" alt="">
          </div>
        </div>
      </div>
    </div>
    <!--回执按钮区域-->
    <div class="btn-area" v-show="showBtn">
      <div class="receipt-btn" :class="{'gray': showGray}" v-text="btnDefault" @click="receiptClick"></div>
    </div>
    <!--发布成功弹框-->
    <div v-show="showToast" class="shadow">
      <em></em>
      <p>已回执</p>
    </div>
    <!--已超时-->
    <div class="timeout" v-show="showTimeout">
      <em></em>
      <p>已超时</p>
    </div>
  </div>
</template>

<script>
import api from '../utils/api';
import vFeed from '../vendor/v-feed';
export default {
  name: 'receive-details',
  components: {
    vFeed
  },
  data () {
    return {
      pictureUrl: [], // 详情图片url
      btnDefault: '回执', // 回执按钮默认状态
      receiveData: null, // 接收数据
      publisher: '', // 发布人
      contentText: '', // 文本内容区域
      showGray: false, // 是否置灰按钮
      showBtn: false, // 是否显示回执按钮
      showToast: false, // 是否展示toast
      showTimeout: false,
      myFeedId: '', // 我的feedId
      friendFeedId: '' // 发布人的feedId
    };
  },
  created () {
    this.getReceiveDetails();
  },
  methods: {
    getReceiveDetails () {
      let params = {
        noticeId: this.$route.query.noId
      };
      api.getReceiveList(params).then((res) => {
        if (res && res.data && res.data.data) {
          const data = res.data.data;
          // 获取发布人
          this.publisher = data.publisher;
          // 获取接收数据
          this.receiveData = data.noticeInfo;
          // 详情图片url
          this.pictureUrl = data.pics;
          // 获取中间文本详情内容
          this.contentText = data.noticeInfo.noContent;
          // 获取我的feedId
          this.myFeedId = data.currentUserFeedId;
          // 获取发布人feedId
          this.friendFeedId = data.publisherFeedId;
        }
      }, (err) => {
        console.log(err);
      });
    },
    // 获取回执时间
    getTime (noReceipt, noReceiptTime) {
      let [time] = [0];
      if (noReceipt === 0) {
        time = 0;
      } else if (noReceipt === 1 && noReceiptTime && noReceiptTime !== 253402271639000) {
        time = noReceiptTime;
      } else if (noReceipt === 1 && noReceiptTime && noReceiptTime === 253402271639000) {
        time = 0;
      }
      return {time};
    },
    /**
     * 获取头像url 类别
     * @param type
     */
    getIconUrl (type) {
      let url = '';
      if (type === 0) {
        url = require('../assets/icon-tz.png');
      } else if (type === 1) {
        url = require('../assets/icon-zy.png');
      } else {
        url = require('../assets/icon-cj.png');
      }
      return url;
    },
    /**
     * 判断是否需要显示回执状态 （根据回执状态 及回执时间判断）
     * @param receipt
     * @param receiptTime
     */
    getReceiptInfo (receipt, receiptTime, nrReceipt) {
      let [flag, receiptImg, setTimeColor] = ['', '', false];
      // 获取当前系统时间戳
      var timestamp = (new Date()).valueOf();
      if (receipt === 0) {
        flag = '-1';
        receiptImg = '';
        this.showGray = true;
        this.btnDefault = '';
        this.showBtn = false;
        setTimeColor = false;
      } else if (receipt === 1 && nrReceipt === 0 && !receiptTime) {
        flag = '0';
        receiptImg = require('../assets/icon-xhz.png');
        this.showGray = false;
        this.btnDefault = '回执';
        this.showBtn = true;
        setTimeColor = false;
      } else if (receipt === 1 && nrReceipt === 0 && receiptTime === 253402271639000) {
        flag = '0';
        receiptImg = require('../assets/icon-xhz.png');
        this.showGray = false;
        this.btnDefault = '回执';
        this.showBtn = true;
        setTimeColor = false;
      } else if (receipt === 1 && nrReceipt === 1 && receiptTime && receiptTime === 253402271639000) {
        flag = '0';
        receiptImg = require('../assets/icon-yhz.png');
        this.showGray = true;
        this.btnDefault = '已回执';
        this.showBtn = true;
        setTimeColor = true;
      } else if (receipt === 1 && nrReceipt === 1 && receiptTime && receiptTime !== 253402271639000) {
        flag = '0';
        receiptImg = require('../assets/icon-yhz.png');
        this.showGray = true;
        this.btnDefault = '已回执';
        this.showBtn = true;
        setTimeColor = true;
      } else if (receipt === 1 && nrReceipt === 0 && receiptTime && receiptTime !== 253402271639000 && receiptTime > timestamp) {
        flag = '1';
        receiptImg = require('../assets/icon-xshz.png');
        this.showGray = false;
        this.btnDefault = '回执';
        this.showBtn = true;
        setTimeColor = false;
      } else if (receipt === 1 && nrReceipt === 0 && receiptTime && receiptTime !== 253402271639000 && receiptTime < timestamp) {
        flag = '0';
        receiptImg = require('../assets/icon-yjz.png');
        this.showGray = true;
        this.btnDefault = '回执';
        this.showBtn = true;
        setTimeColor = true;
      }
      return {flag, receiptImg, setTimeColor};
    },
    /**
     * 获取当前发布人
     * @param publisher
     * @returns {string}
     */
    getPublisher (publisher) {
      return '发布人：' + publisher;
    },
    /**
     * 回执点击
     */
    receiptClick () {
      // 获取当前系统时间戳
      let timestamp = (new Date()).valueOf();
      if (!this.showGray) {
        if (!this.receiveData.noReceiptTime || this.receiveData.noReceiptTime === 253402271639000 || (this.receiveData.noReceiptTime !== 253402271639000 && this.receiveData.noReceiptTime > timestamp)) {
          let params = {
            noticeId: this.$route.query.noId
          };
          api.receiptNotice(params).then((res) => {
            console.log(res);
            this.getReceiveDetails();
            // 请求成功后 更改状态
            this.successStatus();
          }, (err) => {
            console.log(err);
          });
        } else {
          this.getReceiveDetails();
          this.timeOutStatus();
        }
      }
    },
    /**
     * 回执成功后更改状态
     */
    successStatus () {
      this.btnDefault = '已回执';
      this.showGray = true;
      this.showToast = true;
      setTimeout(() => {
        this.showToast = false;
      }, 500);
    },
    /**
     * 已超时状态
     */
    timeOutStatus () {
      this.btnDefault = '回执';
      this.showGray = true;
      this.showTimeout = true;
      setTimeout(() => {
        this.showTimeout = false;
      }, 500);
    }
  }
};
</script>

<style lang="scss">
.container-details{
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background-color: #fff;
  .scroll-wrap {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    overflow: scroll;
  }
  .scroll-wrap-bottom {
    bottom: 55px;
  }
  /*头部名片区域*/
  .card-area{
    width: 100%;
    /*height: 89px;*/
    background-color: #fff;
    /*覆盖 feed 组件样式*/
    .v-feed{
      margin-left: 15px;
      /*height: 89px;*/
      .v-feed__con{
        /*padding-top: 15px;*/
      }
      .v-feed__img{
        margin-left: 0;
        margin-top: -3px;
      }
    }
  }
  /* 中间文本信息区域*/
  .content-text{
    padding: 22px 15px 0 15px;
    /*min-height: 524px;*/
    background-color: #fff;
    .p-title{
      line-height: 40px;
      font-size: 15px;
      color: #383838;
    }
    pre {
      white-space: pre-wrap;
      word-wrap: break-word;
    }
    .p-details{
      line-height: 25px;
      font-size: 15px;
      color: #383838;
    }
    /*详情图片*/
    .details-image{
      margin-top: 15px;
      width: 100%;
      min-height: 300px;
      .img-wrap {
        position: relative;
        padding-top: 50%;
        overflow: hidden;
        margin-bottom: 15px;
        img {
          width: 100%;
          margin-top: -50%;
        }
      }
    }
  }
  /*回执按钮区域*/
  .btn-area{
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    padding: 8px 30px;
    width: 100%;
    height: 54px;
    border-top: 1px solid #eaeaea; /*no*/
    background: rgba(255,255,255,0.90);
    box-shadow: 0 0 4px 0 rgba(119,119,119,0.50);
    .receipt-btn{
      width: 100%;
      height: 38px;
      line-height: 38px;
      text-align: center;
      font-size: 17px;
      border-radius: 4px;
      background: #C3303A;
      color: #FFFFFF;
      &.gray{
        background: #dddee3;
        color: #F7F7F8;
      }
    }
  }
  /*toast 提示*/
  .shadow{
    position: absolute;
    top:50%;
    left: 50%;
    margin-top: -60px;
    margin-left: -60px;
    width: 120px;
    height: 120px;
    background:#000000;
    opacity: 0.5;
    border-radius:5px;
    em{
      margin:15px 0 0 35px;
      /*padding:15px 35px 0 35px;*/
      display: inline-block;
      width:50px;
      height:50px;
      background:url('../assets/toast-rig.png') no-repeat center;
      background-size:100%;
    }
    p{
      margin-top: 3px;
      font-size: 13px;
      color: #FFFFFF;
      letter-spacing: 0;
      text-align: center;
    }
  }
  /*toast 提示*/
  .timeout{
    position: absolute;
    top:50%;
    left: 50%;
    margin-top: -60px;
    margin-left: -60px;
    width: 120px;
    height: 120px;
    background:#000000;
    opacity: 0.5;
    border-radius:5px;
    em{
      margin:15px 0 0 35px;
      /*padding:15px 35px 0 35px;*/
      display: inline-block;
      width:50px;
      height:50px;
      background:url('../assets/icon-timeout.png') no-repeat center;
      background-size:100%;
    }
    p{
      margin-top: 3px;
      font-size: 13px;
      color: #FFFFFF;
      letter-spacing: 0;
      text-align: center;
    }
  }
}
</style>



